.Todo-list-container{
  margin: 5rem;
  display:flex;
  justify-content: center;
  align-items: flex-start;
  width:80rem;
  /* border: 2px solid red; */
  min-height: 50rem;
  background-image: linear-gradient(to left,rgb(97, 217, 233),#10c591);
  /* background-color: skyblue; */
  border-radius: 15rem 0rem 15rem 0rem;
  box-shadow: 5px 5px 5px 5px;
}

.header{
  display: block;
  margin-top: 2%;
  margin-bottom: -3%;
  /* background-color: rgb(81, 194, 232); */
  background-color: rgb(127, 251, 228);
  color: rgb(3, 3, 2);
  padding-left: 35%;
  font-weight: bolder;
  font-size: 2rem;
  border-radius: 0px 50px 0px 50px;
}
.todo-form{
  /* border: 2px solid black; */
  display: flex;
  width: 100%;
  margin-top: 3rem;
  font-size: 2rem;
  gap: 2rem;
}

.todo-form input{
  border: 2px solid rgb(10, 183, 231);
  font-size: 2rem;
  padding: 1rem;
  margin: 1rem;
  border-radius: 1rem;
  text-decoration: none;
  resize: none;
}
.todo-form input:hover{
  border: 3px solid rgb(14, 113, 252);
  font-size: 2rem;
  padding: 1rem;
  margin: 1rem;
  border-radius: 1rem;
  cursor: pointer;
}



.todo-form button{
  height: 50px;
  width: 100px;
  border-radius: 1rem;
  background-color: black;
  color: wheat;
  cursor: pointer;
  font-weight: 600;
  font-size: 2.5rem;
  margin-top: 2%;
  margin-bottom: 2%;
  padding-left: 0%;
  padding-right: 0%;

}



.todo-form button:hover{
  color: black;
  background-image: linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00DDEB);
}









.task-div{
  border-radius: 2px solid brown;
  background-color:aliceblue ;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem;
  border-radius: 2rem;
  box-shadow: 0.5rem 0.5rem 1rem black;
}

.todo-text{
  font-size: 2rem;
  font-weight: bolder;

}

.icons{
  font-size: 2.5rem;
  gap: 3rem;
  font-size: 2.5rem;
  cursor: pointer;

}

.complete{
  /* text-decoration: line-through; */
  opacity: 0.5;
  background-color: rgb(100, 226, 248);
}
.cross{
  height: 30px;
  color: rgb(2, 24, 39);
  font-weight: bolder;
}
.tick{
  height: 30px;
  color: rgb(15, 230, 122);
  font-weight: bolder;
}

.hide{
  display: none;
}